html{
    box-sizing: border-box;
}
*,*::after,*::before{
    box-sizing: inherit;
}
.container{
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}
li{
    list-style: none;
}
.main-header{
    display: block;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #fff;
    z-index: 5;
}
a{
    text-decoration: none;
    color: white;
}
.main-header-list{
    display: flex;
    justify-content: space-between;
    list-style: none;
    align-items: baseline;
}
.header-logo{
    align-self: center;
}

.main-header-item{
    margin: 10px;
}
body{
    font-family: Comfortaa;
}
.header-nav{
    display: flex;
    justify-content: space-between;
}
.header-button{
    color: white;
        border-radius: 19px;
        align-self: flex-start;
        background: linear-gradient( -51deg, rgb(207,99,230) 0%, rgb(170,84,234) 73%, rgb(133,69,238) 100%);
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.33);
        width: 185px;
        border: none;
        font-family: inherit;
        height: 48px;
     
}
/*  Мы - аниматоры */
.main-title{
    padding-top: 150px;
    padding-bottom: 150px;
    background-color: #ffefef;
    position: relative;
 
}
.bc-wave{ 
    position: absolute;
    width: 500px;
    top: -20px;
}
.bc-circle{
    position: absolute;
    right: 0;
    width: 350px;
}
.main-block{
  
    color: white;
    margin: 0 auto;
    background-color: #ad6cbb;
    height: 480px;
    width: 750px;
    border-radius: 50px;
    opacity: 0.89;
    box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
    text-align: center;
    padding: 40px 100px 72px 100px;
}
.main-block h1{
    font-size: 52px;
    margin: 0;
}
.main-block h3{
    font-size: 30px;
}
.main-block h3::after
{
    content: "";
    display: block;
    width: 200px;
    margin: 0 auto;
    margin-top: 15px;
    opacity: 0.5;
    height: 3px;
    background: white;
}
.main-block p{
    font-size: 20px;
}


/* О нас */

.about-us{
    padding-bottom: 150px;
    background-color: #ffefef;
    position: relative;
}
.about-us__img{
    display: inline-block;
    width: 420px;
    height: 480px;
    position: absolute;
    bottom: 0;
    right: 340px;
   
    
}
.about-us__img img{
    width: 100%;
    height: 100%;
    border-radius: 50px 0px 0px 50px;
}
.about-us-block h1{
    font-size: 52px;
    margin: 0;
}
.about-us-block p{
 padding-top: 10px;
 font-size: 20px;
}
.about-us-block h1::after{
    content: "";
    display: block;
    width: 200px;
    margin: 0 auto;
    margin-top: 15px;
    opacity: 0.5;
    height: 3px;
    background: white;
}
.about-us-block{
  display: block;
  position: relative;
    margin: 0 auto;
    color: white;
    width: 340px; 
    height: 560px;
    background-color: #ad6cbb;
    border-radius: 50px 50px 50px 0px;
    opacity: 0.89;
    box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
    text-align: center;
    padding: 20px;
}
.bc-crk{
    position: absolute;
    width: 600px;
    right: 20px;
}

/* CHARACTERS */
.characters{
    background-color: #ffefef;
}
.characters-wrapper{
    position: relative;
    display: inline-block;
    background-color: #ad6cbb;
    border-radius: 50px 50px 0px 0px;
    width: 400px;
    height: 560px;
    color: white;
    opacity: 0.89;
    box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
    text-align: center;
    padding: 20px;
}   
.characters-block{
    max-width: 800px;
    position: relative;
    top: 0;
    left: 30%;
}
.characters-block h1 {
    font-size: 50px;
    margin: 0;
}
.slider-for-img{
    position: absolute;
    left: 380px;
    top: -40px;
    width: 420px;
    height: 480px;
}
.slider-for-img img{
    border-radius: 0px 50px 0px 0px;

}
.characters-nav{
    padding-top: 30px;
    background-color: rgb(105, 49, 119);
    color: white;
    border-radius: 0px 0px 50px 50px;
    left: 0px;
    bottom: 40px;
    width: 820px;
    height: 60px;
}
.characters-nav div{
padding-top: 6px;
}
.bc-circle4{
    position: absolute;
    left: -450px;
    top: 70px;
    width: 450px;
}
.slick-center{
    font-size: 25px;
}